


import React, { useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'

import './_styles.css'


import { UpdateDetails, OpenWatchDialog} from '../actions/common';

import * as _Util from '../store/Util'

import loadable from '@loadable/component'


const Icon2 = loadable(() => import('./Icons'))
// const InputText = loadable(() => import('./InputHr'))

const MenuSlideUpSchedule = loadable(() => import('./menuSlideUpSchedule'))

const MenuSlideUpProcessTime = loadable(() => import('./menuSlideUpProcessTime'))


const useObserveChanges = () => {
  const observeChanges =  useSelector(state => state.observeChanges);
  const dispatch = useDispatch(); 


  const _openMd = (_id, item) => {
    let data = {};
    data['zIndex']=450;
    data['Id']=_id;
    data['observeResize']=true;    
    data['props']={item:item};
    data['content']=<MenuSlideUpProcessTime />;
    OpenWatchDialog(dispatch,data);
  }


  const _openMdSchedule = (_id, item) => {
    let data = {};
    data['zIndex']=450;
    data['Id']=_id;
    data['observeResize']=true;
    data['content']=<MenuSlideUpSchedule />;
    OpenWatchDialog(dispatch,data);
  }




  return {
    observeChanges,
    _openMdSchedule,
    _openMd
  }
}








const Details = (props) => {
  

  const {
    _openMd,
    _openMdSchedule
  } = useObserveChanges();

  const { closePop } = props;

  let _state = _Util.getStore();

  let searchHash = window.location.hash.split('?')[1]?window.location.hash.split('?')[1]:null;
 
  const router = _Util.parseQuery(searchHash);
  let _id = router.id?router.id:null;
  // const typeBrowse = window.location.hash.split('/')[1].split('?')[0];


  let usersFBList = _state["usersFBList"];
  const id  = _id || _state["ActiveUser"];

  const paymentKey  = _state["paymentKey"];

  const paymentKeyID  = _state["paymentKeyID"];

  let servicesAreas = usersFBList && usersFBList[id] && usersFBList && usersFBList[id]["filters"]




  let item = servicesAreas && servicesAreas[paymentKeyID]

  const [ editInput, setEditInputView ] = useState(false); 
  


  const handleKeyUp = (e) => { 

  }
  

 const handleBlur = (e) => { 
    
  }

  const handleChange = (e) => { 
    let ct = e.target.value; 
    let _v = parseInt(ct);
    let h = servicesAreas;
    h[paymentKeyID][paymentKey] = _v;
    UpdateDetails(id, {filters:h})
  }
  
  const _minus = (e) => { 
    let h = servicesAreas;
    let ct = h[paymentKeyID][paymentKey];
    h[paymentKeyID][paymentKey] = parseInt(ct) - 1; 
    UpdateDetails(id, {filters:h})
  }

  const _plus = (e) => {     
    let h = servicesAreas;
    let ct = h[paymentKeyID][paymentKey];
    h[paymentKeyID][paymentKey] = parseInt(ct) + 1; 
    UpdateDetails(id, {filters:h})
    
  }
  

  const handleSaveEdit = (e) => { 
    closePop(1)
  }


  

  let title = paymentKey === "minimunPay" ? "Minimun Pay":"Minimun Pay By Hour";
  let description = paymentKey === "minimunPay" ? "Minimun Pay":"Minimun Pay By Hour";

  return (
      <>
        <div className={`paymentView boxCard`}>
          <div className={`slideWrp`}>
            <div className={'option__edit_payments'}>
            <div className="__body__">   
               <div className={'__title_body__  flexColor _dsplFlx spaceAround' }>
                  {title}                 
               </div>
               <div className={'__title_description__ _dsplFlx spaceAround'}>
                  {description}
               </div>
               <div className={'__minimun__pay__Wrapper__'}>
                  {editInput?
                     <div className={'__minimun__pay__'}>
                        <input type={'number'} value={item && item[paymentKey]} onChange={handleChange} onKeyUp={()=>handleKeyUp()}  onBlur={()=>handleBlur()}/>                     
                     </div>:
                     <div className={'__minimun__pay__ _dsplFlx spaceAround'}  onClick={()=>setEditInputView(true)}>
                        {item && item[paymentKey]}
                     </div> 
                  } 
                  <div className={'__btns__  _dsplFlx spaceAround'}>
                     <div className="flexSpace"/>
                     <div className={'__btn__'}  onClick={()=>_minus()}>
                       <Icon2 name={'minus'} color={'#1967d2'} size={24}/>
                     </div>
                     <div className="flexSpace"/>
                     <div className={'__btn__'}  onClick={()=>_plus()}>
                        <Icon2 name={'add'} color={'#1967d2'} size={24}/>
                     </div>
                     <div className="flexSpace"/>
                  </div>  
                  <div className="btnSavePayments" onClick={()=>handleSaveEdit()} style={{"--color-tab--base--hover":'#777'}}>                     
                     <span className="_dsplFlx spaceAround">{`Save`}</span>              
                  </div>                                
               </div>
            </div>
          </div> 
      </div> 
    </div>   
     
    </>
  );
}  


export default Details

